.app_footer .container{
    padding-right: 5%;
    padding-left: 5%;
}
.app_footer p:hover {
    text-decoration: underline;
    cursor: pointer;
    color: rgb(177, 13, 177);
}

.app_footer .row{
    display: flex;
    padding-top: 40px;
    text-align: center;
    align-items: center;
    justify-content: center;
}
.footer_copyright{
    margin-top: 30px;
    padding: 10px;
    border-top: 1px solid rgb(235, 235, 235);
    border-bottom: 1px solid rgb(235, 235, 235);
    background-color: white;
}
form {
    min-width: 260px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
}
 form input{
    margin-right: 20px;
    border: 1px solid rgb(182, 182, 182);
    padding-left: 15px;
    padding-top: 2px;
    margin-left: 12px;
    padding-bottom: 2px;
    margin-top: 5px;
    margin-bottom: 10px;
    border-radius: 4px;
} 
form button.btn{
    font-family: 'Tajawal', sans-serif;
    font-weight: 400;
    background: transparent;
    border: 1px solid rgb(133, 133, 133);
    padding-top: 5px;
    cursor: pointer;
    color: rgb(95, 95, 95);
    width: 75%;
    margin-left: -6px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 5px;
    border-radius: 4px;
}
form button.btn:hover{
    background: darkviolet;
    color: white;
}
.app_footer img{
    width: 150px;margin-bottom: 40px;
    margin-right: 30px;
}

@media screen and (max-width: 768px){

    form{
        margin-left: 100px;
    }
    
}
@media screen and (min-width: 375px){
    form input{
        min-width: 70%;
    }
    form{
        margin-left: 50px;
    }
}
.footer_copyright h5{
    font-weight: 300;
    font-size: 14px;
    text-align: center;
    font-family: 'Tajawal', sans-serif;
}
.footer_copyright h5 a{
    color: rgb(37, 37, 37);
    font-weight: 500;
    text-decoration: none;
}
.footer_copyright h5 a:hover{
    text-decoration: underline;
    color: darkviolet;
}
.app_footer p{
    text-align: center;
    font-size: 14px;
    color: black;
}
.app_footer{
    background-color: rgb(248, 248, 248);
    position: relative;
    left: 0;
    bottom: -1px;
    width: 100%;
    text-align: center;
    border-top: 1px solid lightgray;
}
.app_navbar{
    border-bottom: 1px solid rgba(211, 211, 211, 0.582);
    width: 100%;
    overflow: hidden;
    background-color: white;
    padding-top: 18px;
    padding-right: 2%;
    padding-bottom: 8px;
    padding-left: 2%;
    display: flex;
    align-items: center;
}   

.pageHeader img{
    max-width: 100px;
    cursor: pointer;
}
.pageHeader{
    flex: 1 1;
}
.pageHamburger{
    cursor: pointer;
}
.pageHamburger .anticon svg:hover{
    color: rgb(231, 90, 189);
}
.pageHamburger .anticon svg{
    font-size: 17px;
}
.modal-side{
    right: 0;
    max-width: 250px;
    top: 36px;
    min-height: 100vh;
    position: absolute;
}

.modal_header{
    display: flex;
    background: white;
    align-items: center;
}
.modal_header p{
    margin-left: 10px;
    font-size: 15px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
}
.modal-header img {
    max-width: 60%;
    padding-left: 40px;
}

.links p{
    font-family: Arial;
    font-size: 15px;
    margin-top: 60px;
    text-align: center;
}
.links p:hover{
    text-decoration: underline;
    color: darkviolet;
}
.cart{
    text-align: center;
    background-color: white;
    align-items: center;
    justify-content: center;
    padding-top: 5px;
    padding-bottom: 5px;
    flex: 1 1;
    display: inline-flex;
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
}
.cart h5:hover{
    color: white;
}
.cart h5{
    padding-top: 10px;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    padding-left: 10px;
}
.cart:hover{
    background-color: darkviolet;
    color: white;
}
.anticon-shopping-cart{
    font-size: 26px;
}

.card h1 {
  font-size: 1.2rem;
  text-transform: capitalize;
}

.product_card .card {
  border-radius: 5px;
  border: 1px solid rgb(231, 231, 231);  
  height: 400px;
  margin-bottom: 20px;
  overflow: hidden;
  max-width: 250px;
}
.product_card .card:hover{
  box-shadow: 0 0px 22px -2px rgba(199, 75, 224, 0.3);
  transition: 0.4s ease-in all;
  transform: translateY(-1%);
}
.card > .card_img{
   max-width: 170px;
   margin-left: auto;
   margin-right: auto;
   margin-top: auto;
   margin-bottom: auto;
}
.min-details h1 span .anticon{
  color: goldenrod;
}
.card > .card_img > img {
  max-width: 150px;
}
.button{
  display: flex;
  align-items: center;
}
.button .MuiIconButton-root:hover{
  color: darkviolet;
}
.button .MuiIconButton-root{
  font-size: 20px;
}
.button button.btn{
  border: 1px solid lightgray;
  flex: 1 1;
  margin-right: 10px;
  font-family: 'Montserrat', sans-serif;
  font-size: 13px;
}
.button button.btn:hover{
  background-color: darkviolet;
  color: white;
}
.details {
  background-color: #FFF;
  border-top: 1px solid rgb(240, 240, 240);
  padding: 20px;
  width: 100%;
  transition: .3s ease-in-out, top .3s ease-in-out;
}
.details > .min-details,
.details > .options,
.details > .options > .options-size,
.details > .options > .options-colors {
  margin-bottom: 20px;
}

.details > .min-details {
  display: flex;
  justify-content: space-between;
}

.details > .min-details > h1 > span {
  color: #7B7B7B;
  display: block;
  font-size: .9rem;
  font-weight: 400;
}

.title h1{
    font-family: 'Spartan', sans-serif;
    font-weight: 500;
    font-size: 22px;
    padding-left: 25px;
}
.title {
    width: 280px;
    margin-bottom: 80px;
    border-radius: 40px;
    display: inline-block;
    padding-top: 22px;
    padding-left: 20px;
    padding-bottom: 20px;
    padding-right: 20px;
    position: relative;
    color: #fff;
    margin-left: 80px;
}


.subtotal{
    margin-top: 60px ;
}
.app_cart{
    display: flex;
    height: 100vh;
}
.cart_left{
    flex: 0.8 1;
    resize: both;
    border-right: 1px solid lightgray;
}
.cart_left h1.cart_heading{
    font-family: 'Alegreya Sans Sc', sans-serif;
    font-weight: 500;
    padding-left: 40px;
    padding-top: 20px;
    padding-bottom: 20px;
    color: black;
    font-size: 26px;
}

.cart_right h1.cart_heading2{
    padding-left: 40px;
    font-family: 'Alegreya Sans Sc', sans-serif;
    font-weight: 500;
    padding-top: 20px;
    padding-bottom: 20px;
    color: black;
    font-size: 26px;
}
.cart_right{
    flex: 0.3 1;
    border-bottom: 1px solid lightgray;
    max-height: 100%;
    padding-left: 50px;
    padding-right: 50px;
    background-color: whitesmoke;
}
.button{
    margin-top: 40px;
}
@media screen and (max-width: 768px){
    .app_cart{
        display: block;
        overflow-y: scroll;
    }
    .product_details_middle{
        display: block;
    }
    .cart_right{
        margin-top: 47px;
        position: relative;
        padding-bottom: 50px;
    }
    .product_details_middle{
        margin-left: 50px;
        margin-right: 50px;
    }
    .cart_product{
        margin-bottom: 80px;
    }
}
@media screen and (max-width: 320px){
    .app_cart{
        display: block;
    }
    .product_details_middle{
        display: block;
    }
    .cart_right{
        margin-top: 47px;
        padding-bottom: 50px;
    }
    .product_details_middle{
        margin-left: 50px;
        margin-right: 50px;
    }
    .cart_product{
        margin-bottom: 80px;
    }
}
.cart_product{
    border: 1px solid lightgray;
    background: white;
    max-width: 250px;
    margin-top: 100px;
    margin-left: 40px;
    padding-top: 20px;
    margin-bottom: 40px;
    padding-bottom: 30px;
}
.cart_product:hover{
    box-shadow: 0px 0px 10px -4px rgba(0, 0, 0, 0.363);
}
.cart_product_img img{
    width: 90%;
    padding: 40px;    
}
.cart_product_heading{
    display: flex;
    width: 100%;
    padding-top: 10PX;
    padding-left: 30px;
    align-items: center;
}
.cart_product_heading h1.product_heading{
    font-family: 'Alegreya Sans Sc', sans-serif;
    font-size: 22px;
    flex: 0.8 1;
}
.cart_product_heading h5.price_heading{
    font-family: 'Alegreya Sans', sans-serif;
    font-weight: 400;
    color: rgb(85, 85, 85);
    font-size: 20px;
}

.product_ratings{
    padding-left: 30px;
    padding-top: 12px;
}
.cart_product_buttons{
    padding-left: 20px;
    padding-top: 25px;
}
.cart_product_buttons button:hover{
    background: darkviolet;
    color: white;
}
.cart_product_buttons button{
    color: darkviolet;
    border: 1px solid darkviolet;
    padding-top: 5px;
    padding-bottom: 5px;
    background: transparent;
    padding-left: 20px;
    border-radius: 4px;
    padding-right: 20px;
}
.product_ratings .anticon{
    color: goldenrod;
}
.checkout_heading h1{
    font-family: 'Alegreya Sans Sc', sans-serif;
    font-size: 30px;
    padding-bottom: 20px;
    border-bottom: 1px solid lightgray;
}
.checkout_heading{
    margin-bottom: 80px;
}
.app_checkout{
    background: rgb(250, 250, 250);
    padding: 50px;
    height: 100%;
    display: flex;
}
.app_checkout .row{
    max-width: 700px;
}
.row input[type="textarea"]{
    resize: both;
    padding-bottom: 40px;
}
.checkout_left{
    flex: 0.2 1;
    margin-right: 20px;
}
.checkout_right{
    flex: 1 1;

}
.button form .btn{
    margin-top: 40px;
}
form {
    background: white;
    padding-left: 40px;
    padding-right: 40px;
    border: 1px solid lightgray;
    padding-top: 20px;
    margin-left: auto;
    margin-right: auto;
    min-width: 300px;
    padding-bottom: 20px;
}
.row input{
    padding-top: 8px;
    border-radius: 2px;box-shadow: none;
    border: 1px solid lightgray;
    padding-bottom: 8px;
}
